import { useQuery } from "react-query";
import { fetchCoinHistory } from "./api";
import ApexChart from "react-apexcharts";
import { useTheme } from "styled-components"; // 테마 가져오기

interface IHistorical {
    close: string;
    volume: string;
    open: string;
    high: string;
    low: string;
    time_open: number;
    time_close: number;
    market_cap: number;
}

interface ChartProps {
    coinId: string;
}

function Chart({ coinId }: ChartProps) {
    const theme = useTheme(); // 현재 테마 가져오기
    const { isLoading, data } = useQuery<IHistorical[]>(["ohlcv", coinId], () =>
        fetchCoinHistory(coinId)
    );

    // 데이터를 candlestick 차트용 형식으로 변환
    const formattedData =
        data?.map((price) => ({
            x: new Date(price.time_close * 1000), // UNIX timestamp를 Date 객체로 변환
            y: [
                parseFloat(price.open),
                parseFloat(price.high),
                parseFloat(price.low),
                parseFloat(price.close),
            ],
        })) || [];

    return (
        <div>
            {isLoading ? (
                "로딩중..."
            ) : (
                <ApexChart
                    type="candlestick"
                    series={[
                        {
                            name: "Price",
                            data: formattedData, // 변환된 데이터를 사용
                        },
                    ]}
                    options={{
                        theme: {
                            mode: "dark",
                        },
                        chart: {
                            height: 500,
                            width: 500,
                            toolbar: {
                                show: false,
                            },
                            background: theme.bgColor, // 테마에서 가져온 배경색
                        },
                        plotOptions: {
                            candlestick: {
                                colors: {
                                    upward: "#fbc531",
                                    downward: "red",
                                },
                            },
                        },
                        xaxis: {
                            type: "datetime", // x축에 시간 데이터 사용
                        },
                        tooltip: {
                            y: {
                                formatter: (value) => `$${value.toFixed(2)}`,
                            },
                        },
                    }}
                />
            )}
        </div>
    );
}

export default Chart;
